import React from 'react'
function filterContents(props) { 
    let filterKey = props.filterKey;   
    //let masterData = props.myContent.value || [];
    //let uniqueArray = removeDuplicates(masterData,filterKey);
    let masterData = props.myContent["@search.facets"] || [];
    let masterFilterData = masterData[filterKey] || [];
    
    return (  
        <div key={"extraDiv"+filterKey} className={props.filterClass+" cd-filter-content"}>          
        {masterFilterData.map((uniqueFilters,index) => (
                 <div className={index > 9 ? "hideFilter ms-CheckBox" : "ms-CheckBox"} key={"ms-CheckBox_"+filterKey+"_"+index}>
                    <input key={"filterChk"+uniqueFilters.value} type="checkbox" className="ms-CheckBox-input"/>
                    <label id={(filterKey || "")+"_"+uniqueFilters.value} key={"filterlbl"+uniqueFilters.value} className="ms-CheckBox-field "
                    name="checkboxa" onClick={(e) => props.parentMethod(uniqueFilters.value, filterKey, e, (filterKey || "")+"_"+uniqueFilters.value)}>
                     <span className="ms-Label" key={"filterSpan"+index}>{(uniqueFilters.value || "")}</span> 
                     <span className="ms-Label lblCount">({(uniqueFilters.count || 0)})</span>
                    </label>
                </div> 
        ))} 
        {/* {(masterFilterData.length > 10) ? <input type="button" onClick={e=>showOrhideFilters(e)} showfilter="1" className="tag tag-entities" value="Show more"/> : ''}        */}
        </div>
    );
}
function showOrhideFilters(e){
    let currentElement = e.target;
    let childrens = e.target.parentNode.children;
    childrens = Array.from(childrens);
    let _className = "hideFilter";
    let filteredChildrens = childrens.filter(child=>child.classList.contains(_className));
    if(currentElement.attributes.showfilter.value === "1")
    {
        filteredChildrens.map(child=>child.style.display = "block");
        currentElement.value = "Show less";
        currentElement.attributes.showfilter.value = "0";         
    }
    else
    {
        filteredChildrens.map(child=>child.style.display = "none");
        currentElement.value = "Show more";
        currentElement.attributes.showfilter.value = "1";
    }
    console.log(e);
}
function removeDuplicates(masterData,filterKey)
{
    let rawArray = [];
    masterData.map((masterFilters) => (
        masterFilters[filterKey].map((subFilters,index) => (
            rawArray.push(subFilters)
        ))
    ));
    const uniqueSet = new Set(rawArray);
    return Array.from(uniqueSet) || [];
}

export default filterContents;