import React from 'react'
import Utility from '../common/utility'
import Tags from './tag-entities'
class MainContent extends React.Component {
    onclickTitle(filePath,e) {
        let decodedPath = Utility.decodeBase64(filePath);
        window.open(decodedPath,"_blank");
    }
    isImageFile(fileNameWithExtn){
        fileNameWithExtn = (fileNameWithExtn || "").toLowerCase().trimEnd();
        var fileExtensionArray = [ "jpeg", "jpg", "gif", "png", "tiff", "bmp", "ppm", "pgm", "pbm", "pnm", "jfif"];
        var ext = fileNameWithExtn.substring(fileNameWithExtn.lastIndexOf(".")+1);
        return (fileExtensionArray.indexOf(ext) > -1) ? true : false;
     }
    renderFileContent(contentKey,objValue)
    {
        let highlightedObj = objValue["@search.highlights"] || "";
        highlightedObj = highlightedObj["merged_content"] || highlightedObj["content"] ||
                         objValue.merged_content || objValue.content || "";
        if(this.isImageFile(objValue.metadata_storage_name))
        {
            return <div className="imgParent">
                <div key={"MainDiv"+contentKey} className="fileContent"
                dangerouslySetInnerHTML={{__html: highlightedObj}}>
                </div>
                <br></br>
                <img key={"imgMain"+contentKey} alt="" onError={ ()=>{
                this.src='../images/Pattern9.jpg'}
                }  className="img-result"
               src={Utility.decodeBase64(objValue.metadata_storage_path)} />
               </div>
        }
        else
        {
            return <div key={"MainDiv"+contentKey} className="fileContent"
            dangerouslySetInnerHTML={{__html: highlightedObj}}></div>;
        }
    }
    render() {
        let fullSearchResult = this.props.myContent;
        let myData = fullSearchResult.value || [];
        
        return (
            <div className="main-cards">
                <h1>{"Total search results : "+(fullSearchResult["@odata.count"] || 0)}</h1>
                {                    
                    Object.keys(myData).map((contentKey) => {
                        const objValue = myData[contentKey];
                        return (
                            <div className="card" key={"card"+contentKey}>
                                <h4 key={"h4"+contentKey} onClick={(e) => this.onclickTitle(objValue.metadata_storage_path, e)} >
                                {(objValue.metadata_storage_name || "").replace(/\.[^/.]+$/, "").toUpperCase()}</h4>
                                {this.renderFileContent(contentKey,objValue)}                                
                                <div className="tagContainer" key={"tagContainer-1"+contentKey}>
                                    <Tags key={"imgTag"+contentKey} iskeyPhrase={false} tags={objValue.People}
                                    parentMethod={this.props.parentMethod}></Tags>
                                <div key={"tagContainer-2"+contentKey}>
                                    <Tags key={"keyPhraseTag"+contentKey} iskeyPhrase={true} tags={objValue.Keyphrases}
                                    parentMethod={this.props.parentMethod}></Tags>
                                </div>
                                </div>
                            </div>
                        );
                    })
                } 
            </div>
        )
    }
}
export default MainContent