import React from 'react';
import axios from 'axios';
class Search extends React.Component {
    static onSearchButtonClick = function(e,isFromLandingPage) {
        let inputText = isFromLandingPage ? e.target : (e.target.offsetParent.children.item(0) ||
                        e.target.offsetParent.children.item[0])
        
        let retObj = {};
        if((inputText.value||"").replace(/\s/g, '')==="")
        {
            retObj={
                        value:"Enter valid Text to search",
                        canSearch: false
                    };
        }
        else
        {
            retObj={
                value:inputText.value,
                canSearch: true
            };
        }
        return retObj;
    };

    static onTagButtonClick = function(e,filterKey) {
        let tagValue = e.target ? e.target.value : "";
        return "$filter="+filterKey+"/any(p: p eq '"+tagValue+"')&";
    };

    static getResults = function(searchKey,filterQuery)
    {
        let baseURL = 'https://azsrch-cognitivesearch.search.windows.net/indexes/azsearch-index/docs?api-version=2019-05-06&$count=true'+
                      '&facet=Keyphrases&facet=Locations&facet=Organizations&facet=People'+
                      "&highlight=content,merged_content&"
        return new Promise((resolve, reject) => {
            axios({
                method: 'GET',
                url: baseURL+filterQuery+searchKey,
                contentType: "application/json",
                async: false,
                headers:{
                  'api-key':'D5BF0358B93DEAC8C8C4979342386611'
                }
              })
              .then((res)=>{
                console.log(res.data)
                resolve(res.data);
              }).catch(
                err=>{
                  console.log(err);
                  this.showOrHideLoader(false);
                  reject();
                }
              )
          })    
    }

  static onclickFilters(subFiltersValue, filterKey, event, element){
      var currentElement = document.getElementById(element);
      var activeClass = "is-checked";
      // currentElement.classList.contains(activeClass) ? 
      //             currentElement.classList.remove(activeClass) : currentElement.classList.add(activeClass);
      // return currentElement.classList.contains(activeClass) ?
      //          "$filter="+filterKey+"/any(p: p eq '"+subFiltersValue+"')&" : "";
      return "$filter="+filterKey+"/any(p: p eq '"+subFiltersValue+"')&";
  }

    static showOrHideLoader(showLoader)
    {
      showLoader ? 
      document.body.classList.add('loading-indicator') :
      document.body.classList.remove('loading-indicator')
    }
}
export default Search;