import React from 'react';
import Search from '../common/search';
function Defaultview(props)
{
    const onKeyPressUP=(e)=>{
        if(e.keyCode===13)
        {
            let objData = Search.onSearchButtonClick(e,true);
            if(objData.canSearch)
            {
               props.history.push('/search/'+objData.value)
            }
            else
            {
                alert(objData.value);
            }
        }
    }
    return(  
        <div className="grid-container grid-container2">      
            <header className="header">
            </header>
            <main className="main main2">  
            <div className="search">
                <input type="checkbox" id="trigger" className="search__checkbox" />
                <label className="search__label-init" htmlFor="trigger"></label>
                <label className="search__label-active" htmlFor="trigger"></label>
                <div className="search__border"></div>
                <input type="text" className="search__input" placeholder="Search..." onKeyUp={e=>onKeyPressUP(e)} />
                <div className="search__close"></div>
            </div>
            </main>
            <footer className="footer">
            <div className="footer__copyright">&copy; 2019 SP COE</div>
            <div className="footer__signature">Made with love by pure genius</div>
        </footer>
        </div>
    )
}

export default Defaultview;
