import React from 'react';
import { HashRouter, BrowserRouter as Router, Route, Redirect } from "react-router-dom";
import './App.css';
import './css/font-awesome.min.css';
import './css/layout.css';
import Defaultview from './components/landingView';
import Searchview from './components/searchView';
function App() {  
  return ( 
    <HashRouter basename='/'>
        <Route exact path={process.env.PUBLIC_URL+"/"} render={() => (
                  <Redirect to="/home"/>
          )}/>
        <Route exact path="/home" component={Defaultview} />
        <Route path="/search/:searchtxt" component={Searchview} />        
    </HashRouter>
  );
}

export default App;
