import React,{useState,useEffect} from 'react';
import Toggle from './toggle';
import Content from './content';
import Filtercontent from './filter';
import search from '../common/search';
function Searchview(props) {
    const [searchData, updateData] = useState([]);
    useEffect(()=>{  
      let searchText = props.match.params.searchtxt || "";
      search.showOrHideLoader(true);
      search.getResults('search='+searchText,"")
            .then((result) => {
              updateData(result);
              search.showOrHideLoader(false);
            })
    },[updateData]);
  
    const reRender = (e) => {
      search.showOrHideLoader(true);
      let objData =search.onSearchButtonClick(e,false);
      if(objData.canSearch)
      {
        updateScreen('search='+objData.value,"");
      }
      else
      {
        search.showOrHideLoader(false);
        alert(objData.value);
      }
    }

    const updateScreen =(searchText,filterText)=>{
      search.getResults(searchText,filterText)
            .then((result) => {
              updateData(result);
              search.showOrHideLoader(false);
            })
    }
  
    const onFilterClick = (subFilters, filterKey, event, element)=>{
      search.showOrHideLoader(true);
      let filterQuery = search.onclickFilters(subFilters, filterKey, event, element);
      let searchValue = document.getElementsByClassName('searchInput')[0].value ||
                        props.match.params.searchtxt || "";
      updateScreen('search='+searchValue,filterQuery);
    }

    const onTagClick = (event, filterKey)=>{
      search.showOrHideLoader(true);
      let filterQuery = search.onTagButtonClick(event, filterKey);
      let searchValue = document.getElementsByClassName('searchInput')[0].value ||
                        props.match.params.searchtxt || "";
      updateScreen('search='+searchValue,filterQuery);
    }

    const goBackHome=(e)=>{
        props.history.push('/home/');
    }

    const onKeyPressUP=(e)=>{
      if(e.keyCode===13)
      {
        reRender(e);
      }
  }
  
    return (
        <div className="grid-container">
        <Toggle></Toggle>
        <header className="header">
            <a className="goBackHome" onClick={goBackHome}>
                <span><i className="fa fa-home" aria-hidden="true"></i></span>
            </a>
        </header>
          <aside className="sidenav">
          <div className="searchBox">
            <input className="searchInput"type="text" onKeyUp={(e)=>onKeyPressUP(e)} placeholder="Search"/>
            <button className="searchButton" onClick={(e)=>reRender(e)}>
                <i className="material-icons">
                    search
                </i>
            </button>
          </div>
          <div className="sidenav__close-icon">
              <i className="fa fa-times"></i>
          </div>
          <ul className="sidenav__list">
            <Filtercontent myContent={searchData} parentMethod={onFilterClick}></Filtercontent>
          </ul>
          </aside>
          <main className="main">  
                <Content myContent={searchData} parentMethod={onTagClick}></Content>
          </main>
          <footer className="footer">
            <div className="footer__copyright">&copy; 2019 SP COE</div>
            <div className="footer__signature">Made with love by pure genius</div>
        </footer>
    </div>
    );
  }

  export default Searchview;