import React from 'react'
import filterConfig from '../config/filterConfig.json'
import Subfilters  from './sub-filter'
function filterEntity(props) {
        return (  
            <div id="facet-nav" className="cd-filter-block" key="filterMaster">          
            {filterConfig.map((tag,index) => (
                <div className="filter-list" id={"filterSection"+index} key={(tag.internalName || "").toLowerCase()+"-facets"}>
                <h5 className={index!==0 ? 'facet-header closed':'facet-header'} onClick={e=>onClickCollapisibleFilters(e)}
                 key={"filter_"+tag.internalName}>{(tag.displayName || "").toUpperCase()}</h5>
                   <Subfilters filterKey={tag.internalName} myContent={props.myContent} 
                            parentMethod={props.parentMethod}        filterClass={index!==0 ? 'collapseFilter':''}></Subfilters>                    
                </div>
            ))}
            </div>
        );
    }

function onClickCollapisibleFilters(e)
{
    var _className = 'collapseFilter';
    var _headerClass = 'closed';
    var childElement = e.target.parentNode.lastChild;
    childElement.classList.contains(_className) ? childElement.classList.remove(_className) :
                                                childElement.classList.add(_className);
    e.target.classList.contains(_headerClass) ? e.target.classList.remove(_headerClass) :
                                    e.target.classList.add(_headerClass);
    console.log(e);
}

export default filterEntity;
